import axios from 'axios'
import store from '@/store'
import {
  domain
} from './domain.js'
import Vue from 'vue'
import timestamp from 'time-stamp';

export {
  wxShare
}

//
const wxShare = (obj, callback) => {
  function getUrl() {
    let url = "";
    // ios
    if (window.__wxjs_is_wkwebview === true) {
      url = store.state.comm.indexUrl.split("#")[0];
    } else {
      url = window.location.href.split("#")[0];
    }
    console.log('wxUrl:', url)
    return url;
  }
  if (obj) {
    var title = obj.title == undefined || obj.title == null ? '' : obj.title;
    var link = obj.link == undefined || obj.link == null ? window.location.href : obj.link;
    var desc = obj.desc == undefined || obj.desc == null ? '' : obj.desc;
    var imgUrl = obj.imgUrl == undefined || obj.imgUrl == null ? 'src/img/share.png' : obj.imgUrl;
    var debug = obj.debug == true ? true : false;
  } else {
    alert('');
  }
  //
  //   axios.post(`${domain}/nine/wxshared?url=${getUrl()}`)
  axios.get(`https://m.xiepinhui.com.cn/nine/wxshared?url=${encodeURIComponent(getUrl())}`)
    .then(res => {
      let {
        code,
        data
      } = res.data
      if (code === 2000) {
        let timestamp = data.timestamp;
        let nonceStr = data.noncestr;
        let jsapi_ticket = data.jsapi_ticket;
        let signature = data.sign;
        let appId = data.app_id;
        // let appId = 'wxa4142cc3047c6dff';
        Vue.wechat.config({
          debug: true,
          appId,
          timestamp,
          nonceStr,
          signature,
          jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
        });
      }
      Vue.wechat.error(function (err) {
        console.log('err', err)
      })
      Vue.wechat.ready(function () {
        console.log('ready')
        //
        Vue.wechat.onMenuShareTimeline({
          title: title, // 
          link: link, // 
          desc: desc, // 
          imgUrl: imgUrl, // 
          success: function () {
            callback && callback();
            // 
          },
          cancel: function () {
            // 
          }
        });
        // Vue.wechat.chooseWXPay({
        //   timestamp: 0, // jssdktimestamptimeStampS
        //   nonceStr: '', //  32 
        //   package: '', // prepay_idprepay_id=\*\*\*
        //   signType: '', // 'SHA1''MD5'
        //   paySign: '', // 
        //   success: function (res) {
        //     // 
        //   }
        // });
        // //
        // Vue.wechat.onMenuShareAppMessage({
        //   title: title, // 
        //   desc: desc, // 
        //   link: link, // 
        //   imgUrl: imgUrl, // 
        //   type: '', // ,musicvideolinklink
        //   dataUrl: '', // typemusicvideo
        //   success: function () {
        //     // 
        //     callback && callback();
        //   },
        //   cancel: function () {
        //     // 
        //   }
        // });
        // //QQ
        // Vue.wechat.onMenuShareQQ({
        //   title: title, // 
        //   desc: desc, // 
        //   link: link, // 
        //   imgUrl: imgUrl, // 
        //   success: function () {
        //     // 
        //     callback && callback();
        //   },
        //   cancel: function () {
        //     // 
        //   }
        // });
        // //QQ
        // Vue.wechat.onMenuShareQZone({
        //   title: title, // 
        //   desc: desc, // 
        //   link: link, // 
        //   imgUrl: imgUrl, // 
        //   success: function () {
        //     // 
        //     callback && callback();
        //   },
        //   cancel: function () {
        //     // 
        //   }
        // });
      })
    })
}



